@font-face {
  font-family: 'S-CoreDream-4Regular';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_six@1.2/S-CoreDream-4Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;500;600;700;800;900&family=Roboto:wght@300;400;500;600;700;800;900&display=swap');

@keyframes fallback {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.fallback-box {
  height: 600px;
  background-color: rgb(0, 0, 0, 0.05);
  animation: 0.5s ease infinite fallback;
}
